<template>
  <div class="deal">
    <el-row :gutter="20" class="functionalArea">
      <el-col :span="6" :offset="18">
        <el-input placeholder="请输入内容" v-model="searchInput" clearable>
          <template slot="append">
            <el-button type="success" plain>搜索</el-button>
          </template>
        </el-input>
      </el-col>
    </el-row>

    <el-table
            ref="multipleTable"
            :data="tableData"
            style="width: 100%"
            class="elTableOne"
            :header-cell-style="headerCellStyle"
            :cell-style="cellStyle"
            @select="handleSelectionChange"
            @select-all="selectAllFn"
    >
      <el-table-column
              type="selection"
              width="55">
      </el-table-column>
      <el-table-column
              label="序号"
              width="60">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.index }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="流水号"
              width="180">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>主题: {{ scope.row.subject }}</p>
            <p>身份证: {{ scope.row.idno }}</p>
            <p>流程: {{ scope.row.process }}</p>
            <p>时限: {{ scope.row.timeout }}天</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium"  @click="seeProcess(scope.row)">{{ scope.row.water_num }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
              label="事项"
              width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.item }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="申请人/申请单位"
              width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.p_name }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="申请项目"
              width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.p_project }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="已用时(天)"
              width="100">
        <template slot-scope="scope">
          <el-button size="mini" :type="(scope.row.used_time > scope.row.timeout)? 'danger' : ''">{{ Math.floor(scope.row.used_time) }}</el-button>
        </template>
      </el-table-column>
      <el-table-column
              label="操作"
              min-width="140">
        <template slot-scope="scope">
          <el-button
                  size="mini"
                  icon="el-icon-check"
                  circle
                  :disabled="scope.row.isOpen"
                  @click="pass(scope.$index, scope.row)"></el-button>
          <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-close"
                  circle
                  :disabled="scope.row.isOpen"
                  @click="unPass(scope.$index, scope.row)"></el-button>
          <router-link :to="toNext">
            <el-button
                    size="mini"
                    icon="el-icon-view"
                    circle
                    :disabled="scope.row.isOpen"
                    @click="seeReference(scope.$index, scope.row)"></el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <div class="block pagination">
      <el-pagination
              v-if="curPageShow"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage3"
              :page-size="pageSize"
              layout="prev, pager, next, jumper"
              :total="totalSize">
      </el-pagination>
    </div>

    <el-dialog title="流程信息" :visible.sync="processVisible" width="80%">
      <el-table
              ref="multipleTable1"
              :data="tableData1"
              style="width: 100%"
              :header-cell-style="headerCellStyle"
              :cell-style="cellStyle"
      >
        <el-table-column
                label="序号"
                width="60">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.index }}</span>
          </template>
        </el-table-column>
        <el-table-column
                label="流程"
                width="100">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.current_process }}</span>
          </template>
        </el-table-column>
        <el-table-column
                label="处理人"
                width="180">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.user }}</span>
          </template>
        </el-table-column>
        <el-table-column
                label="收到时间"
                width="180">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.starts }}</span>
          </template>
        </el-table-column>
        <el-table-column
                label="处理时间"
                width="180">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.ends }}</span>
          </template>
        </el-table-column>
        <el-table-column
                label="处理状态"
                width="100">
          <template slot-scope="scope">
            <span style="margin-left: 10px" v-if="scope.row.result === 'Y'">通过</span>
            <span style="margin-left: 10px" v-if="scope.row.result === 'N'">未通过</span>
          </template>
        </el-table-column>
        <el-table-column
                label="操作"
                min-width="240">
          <template slot-scope="scope">
            <el-button
                    size="mini"
                    @click="seeMsg(scope.$index, scope.row)">查看留言</el-button>
            <!--<el-button-->
                    <!--size="mini"-->
                    <!--type="danger"-->
                    <!--@click="handleDelete(scope.$index, scope.row)">不合格</el-button>-->
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="processVisible = false">取 消</el-button>
        <el-button type="primary" @click="addThemeSureFn">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog
            title="备注未通过原因"
            :visible.sync="unpassVisible"
            width="800px">
      <div>备注：选择“其他”时，可以手输未通过原因</div>
      <el-checkbox-group v-model="checkList">
        <el-row>
          <el-col class="checkBox" :span="12" v-for="(item, index) in errorList" :key="index"><el-checkbox :label="item.label" :val="item.val"></el-checkbox></el-col>
        </el-row>
      </el-checkbox-group>
      <el-input
              type="textarea"
              class="textArea"
              :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="请输入未通过原因"
              v-model="textarea3"
              v-if="IsTextarea">
      </el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="unpassVisible = false">取 消</el-button>
        <el-button type="primary" @click="unpassSureFn">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog
            title="查看未通过原因"
            :visible.sync="unpassVisible1"
            width="800px">
      <div>备注：选择“其他”时，可以手输未通过原因</div>
      <el-checkbox-group v-model="checkList1">
        <el-row>
          <el-col class="checkBox" :span="12" v-for="(item, index) in errorList" :key="index"><el-checkbox :label="item.label" :val="item.val" disabled></el-checkbox></el-col>
        </el-row>
      </el-checkbox-group>
      <el-input
              type="textarea"
              class="textArea"
              :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="请输入未通过原因"
              v-model="textarea4"
              v-if="IsTextarea1"
              disabled>
      </el-input>
      <div slot="footer" class="dialog-footer">
        <!--<el-button @click="unpassVisible1 = false">取 消</el-button>-->
        <el-button type="primary" @click="unpassVisible1 = false">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import { baseUrl } from '@/api/unsplash/baseUrl'
import supportFun from '@/supportFunction/index'
export default {
  name: 'ATheme',
  data () {
    return {
      searchInput: '',
      tableData: [],
      tableData1: [],
      baseUrl: baseUrl,
      toNext: '/reference/element',
      areaList: [
        {
          label: '投资项目',
          val: '投资项目'
        },
        {
          label: '社会事业',
          val: '社会事业'
        },
        {
          label: '工商登记',
          val: '工商登记'
        }
      ],

      processVisible: false,
      formLabelWidth: '120px',
      form: {
        name: '',
        area: '投资项目',
        seq: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      fixThemeFormVisible: false,
      form1: {
        name: '',
        area: '投资项目',
        seq: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },

      errorList: [
        {
          val: '证明材料及相关资料前后不一致',
          label: '证明材料及相关资料前后不一致'
        },
        {
          val: '许可审批量化指标不达标',
          label: '许可审批量化指标不达标'
        },
        {
          val: '现场踏勘不达标',
          label: '现场踏勘不达标'
        },
        {
          val: '证明材料及相关资料模糊不清，无法辨认',
          label: '证明材料及相关资料模糊不清，无法辨认'
        },
        {
          val: '相关证件不在有效期或无效',
          label: '相关证件不在有效期或无效'
        },
        {
          val: '许可审批不在有效期',
          label: '许可审批不在有效期'
        },
        {
          val: '申请表填写不正确',
          label: '申请表填写不正确'
        },
        {
          val: '照片不符合要求',
          label: '照片不符合要求'
        },
        {
          val: '公章加盖不规范',
          label: '公章加盖不规范'
        },
        {
          val: '其他',
          label: '其他'
        }
      ],

      unpassVisible: false,
      checkList: [],
      textarea3: '',
      IsTextarea: false,

      unpassVisible1: false,
      checkList1: [],
      textarea4: '',
      IsTextarea1: false,
      unpassRow: {},

      selectThemeIds: [],

      currentPage3: 1,
      curPageShow: true,
      pageSize: 0,
      totalSize: 0
    }
  },
  watch: {
    checkList: function (val) {
      this.checkList = val
      if (val.indexOf('其他') > -1) {
        this.IsTextarea = true
      } else {
        this.IsTextarea = false
      }
    },
    unpassVisible: function (val) {
      if (!val) {
        this.checkList = []
      }
    }
  },
  created: function () {
    let temp = {
      page: 1,
      user_id: sessionStorage.user_id
    }
    this.getDealFn(temp)
  },
  methods: {
    addThemeSureFn () {
      let temp = {
        name: this.form.name.replace(/(^\s*)|(\s*$)/g, ''),
        area: this.form.area.replace(/(^\s*)|(\s*$)/g, ''),
        seq: this.form.seq.replace(/(^\s*)|(\s*$)/g, ''),
        user_id: sessionStorage.user_id
      }
      if (temp.name !== '' && temp.seq !== '') {
        this.addThemeFn(temp)
      } else {
        this.notifyFn('提示', '请补全信息')
      }
    },
    fixThemeSureFn () {
      let temp = {
        name: this.form1.name.replace(/(^\s*)|(\s*$)/g, ''),
        area: this.form1.area.replace(/(^\s*)|(\s*$)/g, ''),
        seq: this.form1.seq.replace(/(^\s*)|(\s*$)/g, ''),
        id: this.form1.id,
        user_id: sessionStorage.user_id
      }
      if (temp.name !== '' && temp.seq !== '') {
        this.fixThemeFn(temp)
      } else {
        this.notifyFn('提示', '请补全信息')
      }
    },
    clearThemesClick () {
      if (this.selectThemeIds.length) {
        let params = {
          id_list: this.selectThemeIds,
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      } else {
        this.notifyFn('提示', '未选择主题')
      }
    },
    pass (index, row) {
      console.log(index, row)
      this.$confirm(row.current_process + '流程内将通过审核, 是否继续?', '通过', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        row.deal_status = 'Y'
        row.user_id = sessionStorage.user_id
        this.postIsPassFn(row)
      }).catch(() => {
        this.notifyFn('提示', '已取消')
      })
    },
    unPass (index, row) {
      this.unpassVisible = true
      this.unpassRow = row
      console.log(row)
    },
    seeProcess (row) {
      let temp = {
        water_num: row.water_num,
        user_id: sessionStorage.user_id
      }
      this.postProcessInfoFn(temp)
      this.processVisible = true
      console.log(temp)
    },
    seeMsg (index, row) {
      console.log(row)
      if (row.discription === '') {
        this.notifyFn('提示', '没有留言')
      } else {
        this.unpassVisible1 = true
        let tempObj = JSON.parse(row.discription)
        console.log(tempObj)
        if (tempObj.isOther) {
          // 选择其他
          tempObj.checks.push('其他')
          this.IsTextarea1 = true
          this.checkList1 = tempObj.checks
          this.textarea4 = tempObj.msg
        } else {
          this.IsTextarea1 = false
          this.checkList1 = tempObj.checks
          this.textarea4 = tempObj.msg
        }
      }
    },
    unpassSureFn () {
      let tempObj = {
        checks: [],
        isOther: false,
        msg: ''
      }
      let temp = this.checkList.slice()
      if (temp.indexOf('其他') > -1) {
        temp.splice(temp.indexOf('其他'), 1)
        tempObj = {
          checks: temp,
          isOther: true,
          msg: this.textarea3
        }
      } else {
        tempObj = {
          checks: temp,
          isOther: false,
          msg: ''
        }
      }
      let row = this.unpassRow
      this.$confirm(row.current_process + '流程内将不通过审核, 是否继续?', '不通过', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        row.deal_status = 'N'
        row.description = JSON.stringify(tempObj)
        row.user_id = sessionStorage.user_id
        this.unpassVisible = false
        this.postIsPassFn(row)
      }).catch(() => {
        this.notifyFn('提示', '已取消')
      })
    },
    seeReference (index, row) {
      console.log(row)
      var obj = JSON.parse(sessionStorage.reference)
      obj.curTheme = row.subject
      obj.curMatter = row.item
      sessionStorage.reference = JSON.stringify(obj)
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      let temp = {
        page: val,
        user_id: sessionStorage.user_id
      }
      this.getDealFn(temp)
    },
    async getDealFn (params) {
      try {
        let res = await this.$api.unsplash.getDeals(params)
        let temp = []
        if (res.errno === '0') {
          let temp1 = res.data.in_deal.slice()
          let temp2 = res.data.wait_deal.slice()
          this.pageSize = res.data.size
          this.totalSize = res.data.total
          for (let i = 0; i < temp1.length; i++) {
            temp1[i].isOpen = false
          }
          for (let i = 0; i < temp2.length; i++) {
            temp2[i].isOpen = true
          }
          temp = temp.concat(temp1, temp2)
          this.tableData = supportFun.addSequence(temp)

          let self = this
          setTimeout(function () {
            self.toggleSelection(self.tableData.slice(0, temp1.length))
          }, 300)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async postProcessInfoFn (params) {
      try {
        let res = await this.$api.unsplash.postProcessInfo(params)
        console.log(1, res)
        if (res.errno === '0') {
          // for (let i = 0; i < res.data.length; i++) {
          //   // res.data[i].starts = supportFun.formatUnixtimestamp(res.data[i].starts)
          //   // res.data[i].ends = supportFun.formatUnixtimestamp(res.data[i].ends)
          // }
          this.tableData1 = res.data
          this.tableData1 = supportFun.addSequence(this.tableData1)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    handleSelectionChange (selection, row) { // 单击
      console.log(12, selection, row)
      let haveSelection = []
      for (let i = 0; i < selection.length; i++) {
        haveSelection.push(selection[i].index)
      }
      if (haveSelection.indexOf(row.index) > -1) {
        // console.log('Y')
        let temp = {
          water_num: row.water_num,
          user_id: sessionStorage.user_id
        }
        this.postActiveFn(temp)
        this.tableData[row.index - 1].isOpen = false
      } else {
        // console.log('N')
        let temp = {
          water_num: row.water_num,
          user_id: sessionStorage.user_id
        }
        this.postUnActiveFn(temp)
        this.tableData[row.index - 1].isOpen = true
      }
    },
    selectAllFn (selection) { // 全选
      console.log(selection)
      this.$refs.multipleTable.clearSelection()
    },
    async postActiveFn (params) {
      try {
        let res = await this.$api.unsplash.postActive(params)
        // 勾勾 需要修改
        if (res.errno === '0') {
          this.notifyFn('提示', '激活所选任务', 'success')
        } else if (res.errno === '4003') {
          this.notifyFn('提示', '所选任务被' + res.user.name + '激活')
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async postUnActiveFn (params) {
      try {
        let res = await this.$api.unsplash.postUnActive(params)
        console.log(1, res)
        if (res.errno === '0') {
          this.notifyFn('提示', '该任务撤销激活状态', 'success')
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async postIsPassFn (params) {
      try {
        let res = await this.$api.unsplash.postIsPass(params)
        console.log(1, res)
        if (res.errno === '0') {
          if (params.deal_status === 'Y') {
            this.notifyFn('提示', '完成' + '流水号：' + params.water_num + '在' + params.current_process + '流程的任务', 'success')
          } else {
            this.notifyFn('提示', '流水号：' + params.water_num + '在' + params.current_process + '流程中未通过', 'success')
          }
          let temp = {
            page: 1,
            user_id: sessionStorage.user_id
          }
          this.getDealFn(temp)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex >= 0) {
        return {
          'text-align': 'center'
        }
      }
    },
    myAlert (txt, title, self) {
      self.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    notifyFn (title, msg, type = 'warning') {
      this.$notify({
        title: title,
        message: msg,
        type: type,
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    }
  }
  // beforeRouteLeave (to, from, next) {
  //   // console.log(to)
  //   // 设置下一个路由的 meta
  //   // to.meta.keepAlive = false // C 跳转到 A 时让 A 不缓存，即刷新
  //   // next()
  // }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .deal{
    width: 100%;
    min-height: 100%;
    position: relative;
    .functionalArea{
      height: 80px;
    }
    .headerCellStyle{
      text-align: center;
    }
    .elTableOne tr:first-child th:first-child .el-checkbox__inner{
      display: none;
    }
    .el-button.is-circle {
      margin-left: 0;
    }
    .checkBox{
      margin-top: 10px;
      margin-bottom: 10px;
      text-align: left;
      padding-left: 40px;
    }
    .textArea{
      margin-top: 10px;
    }
    .pagination{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 0%);
    }
  }
</style>



